import Cart from '../components/Cart'
import { useDispatch } from 'react-redux';
import { decrement, delate } from '../store/cartSlice';

function CartList() {
  const dispatch = useDispatch();

  function clickHandler(event) {
    event.preventDefault();
    let t = event.target;
    if (!t.classList.contains('minus-one')) return true;
    dispatch(decrement(t.getAttribute('data-key')));
  }

  function clickDelete(event) {
    event.preventDefault();
    let t = event.target;
    if (!t.classList.contains('remove-from-cart')) return true;
    dispatch(delate(t.getAttribute('data-key')));
  }

  function bothFunc(event) {
    clickHandler(event);
    clickDelete(event);
  }

  return (
    <div className="cart-wrap" onClick={bothFunc}>
      <Cart />
    </div>

  );
}

export default CartList;