import React from 'react';
import { useSelector } from 'react-redux';
import { selectCart } from '../store/cartSlice';
import { selectGoods } from '../store/goodsSlice';


function Cart() {
  const goods = useSelector(selectGoods);
  const cart = useSelector(selectCart);

  const goodsObj = goods.reduce((accum, item) => {
    accum[item['articul']] = item;
    return accum;
  }, {})

  let sum = 0;
  Object.keys(cart).map(item => sum += goodsObj[item]['cost'] * cart[item]);

  return (
    <div>
      <table border="1">
        <tbody>
          <tr><th>Product</th><th>Image</th><th>Price (one thing)</th><th>Quantity</th><th>Goods cost of all</th><th>Minus / Remove product</th></tr>
          {Object.keys(cart).map(item => <tr key={item + goodsObj[item]['title']}><td>{goodsObj[item]['title']}</td><td><img className="smallPic" src={goodsObj[item]['image']} alt="" /></td><td>{goodsObj[item]['cost']}</td><td>{cart[item]}</td><td>{cart[item] * goodsObj[item]['cost']}</td><td><button data-key={goodsObj[item]['articul']} className="minus-one">Minus</button><button data-key={goodsObj[item]['articul']} className="remove-from-cart">Remove</button></td></tr>)}
        </tbody>
      </table>
      <p className="cost">Total cost: <span>{sum}</span></p>
    </div>
  );
}
export default Cart;